.navbar {
  height: 70px;
  // border-bottom: 0.5px solid rgb(230, 227, 227);
  display: flex;
  left: 244px;
  align-items: center;
  font-size: 14px;
  background-color: #191c24;
  color: #ffff;

  .wrapper {
    display: flex;
    align-items: center;
    width: 100%;
    padding: 20px;
    justify-content: space-between;

    .search {
      display: flex;
      align-items: center;
      border: 1px solid #2c2e33;
      border-radius: 100px;
      padding: 3px;

      input {
        border: none;
        outline: none;
        background: transparent;

        ::placeholder {
          font-size: 12px;
        }
      }
    }

    .items {
      display: flex;
      align-items: center;

      .item {
        display: flex;
        align-items: center;
        margin-right: 20px;
        position: relative;

        .icon {
          font-size: 20px;
        }

        .counter {
          width: 15px;
          height: 15px;
          background-color: red;
          border-radius: 50%;
          color: white;
          display: flex;
          align-items: center;
          justify-content: center;
          font-size: 10px;
          font-weight: bold;
          position: absolute;
          top: -5px;
          right: -5px;
        }

        .avatar {
          width: 30px;
          height: 30px;
          border-radius: 50%;
        }
      }
    }
  }
}
